body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin-block-end: 0;
}

.subheading {
  line-height: var(--wp--custom--line-height--tight);
  font-weight: 500;
  letter-spacing: var(--wp--custom--letter-spacing--tighter);
}

em {
  font-weight: 400;
}

strong {
  font-weight: 600;
  color: var(--wp--custom--color--text--primary);
