.image-carousel {
  border-radius: var(--wp--custom--border-radius--md);
  overflow: hidden;
  margin-block-start: var(--wp--preset--spacing--4-xl);
}

.wp-block-pulsar-carousel-slide__background-image {
  position: relative;
}

.wp-block-pulsar-carousel .splide__arrow {
  height: 64px;
  width: 64px;
  background: rgba(12, 12, 12, 0.5);
  backdrop-filter: blur(2px);
  border-radius: 1000px;
}
.wp-block-pulsar-carousel .splide__arrow:hover {
  transform: scale(1.1);
  transition: transform 0.4s ease-in-out;
  background: rgba(12, 12, 12, 0.7);
}
.wp-block-pulsar-carousel .splide__arrow:not(:hover) {
  transform: scale(1);
  transition: transform 0.4s ease-in-out;
}
.wp-block-pulsar-carousel .splide__arrow::before {
  background-color: var(--wp--custom--color--surface--default);
  height: 32px;
  width: 32px;
}

.wp-block-pulsar-carousel .splide__arrow--prev {
  margin-left: var(--wp--preset--spacing--lg);
}

.wp-block-pulsar-carousel .splide__arrow--next {
  margin-right: var(--wp--preset--spacing--lg);
}

.wp-block-pulsar-carousel .splide__arrow--prev::before {
  mask-image: url("../../assets/svg/arrow-left.svg");
}

.wp-block-pulsar-carousel .splide__arrow--next::before {
  mask-image: url("../../assets/svg/arrow-right.svg");
}

.quote-card {
  background-color: var(--wp--custom--color--surface--accent);
  border-radius: var(--wp--custom--border-radius--md);
