}

.page-navigation-block::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1001; /* Ensure it overlays the content */
  background: transparent; /* Keeps it invisible but clickable */
