}

.contact-container {
  border-radius: var(--wp--custom--border-radius--lg);
  max-width: var(--wp--custom--layout--wide);
  width: 100%; /* Allow responsiveness */
  overflow: hidden;
  box-sizing: border-box; /* Prevent padding from affecting width */
  margin-block: auto; /* Auto vertical margins for centering */
  z-index: 2;
}
.contact-container::before {
  content: "";
  position: absolute;
  top: 10vh;
  right: -410px;
  transform: translateY(-50%);
  width: 1200px;
  height: 1200px;
  background-image: url(../../assets/svg/vector.svg); /* Replace with your phone icon image path */
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
  opacity: 0.5;
}
.contact-container::after {
  content: "";
  position: absolute;
  bottom: -70vh;
  left: -410px;
  transform: translateY(-50%);
  width: 1200px;
  height: 1200px;
  background-image: url(../../assets/svg/vector.svg); /* Replace with your phone icon image path */
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -2;
  opacity: 0.5;
}

.contact-holder {
  display: flex;
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
  height: 100vh;
  overflow: hidden;
  position: relative; /* Add this line to make the div a positioning context */
}

.contact-holder svg {
  position: absolute; /* Add this line to position the svgs */
  /* Add your desired positioning properties here */
}

.forminator-guttenberg {
  width: 100% !important;
  margin-block-start: 0;
}

.forminator-error-message {
  background-color: transparent !important;
  font-size: var(--wp--custom--font-size--md) !important;
  padding-left: var(--wp--preset--spacing--xl) !important;
  margin-block-start: var(--wp--preset--spacing--xs) !important;
  display: inline-block !important;
  color: var(--wp--custom--color--text--error) !important;
  font-weight: 500 !important;
}

.forminator-row {
  margin-bottom: var(--wp--preset--spacing--sm) !important;
}

.send-form {
  align-items: center;
  border: unset;
  border-radius: unset;
  box-shadow: unset;
  cursor: pointer;
  display: inline-flex;
  gap: var(--wp--preset--spacing--2-xs);
  justify-content: center;
  padding: 16px 32px;
  text-align: center;
  transition: all var(--wp--custom--transition-duration--quick);
  font-weight: 500;
  background-color: var(--wp--custom--color--surface--invert, #fff);
  border: 2px solid var(--wp--custom--color--text--primary, #000);
  border-radius: 999px;
  color: var(--wp--custom--color--text--invert, #000);
  overflow: hidden;
  position: relative;
  display: inline-block;
  transition: transform 0.4s ease-out;
  z-index: 1;
  margin-top: var(--wp--preset--spacing--md);
}
.send-form:hover {
  transform: scale(1.05);
}

.text-icon {
  position: relative;
  padding-left: 40px; /* Adjust the padding as needed */
}
.text-icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 24px; /* Adjust the width and height as needed */
  height: 24px;
  background-image: url(../../assets/svg/phone.svg); /* Replace with your phone icon image path */
  background-repeat: no-repeat;
  background-size: cover;
}
.text-icon.email::before {
  background-image: url(../../assets/svg/email.svg); /* Replace with your email icon image path */
}

.container {
  padding-left: var(--wp--style--root--padding-left);
  padding-right: var(--wp--style--root--padding-right);
}
.container > :where(:not(.alignleft):not(.alignright):not(.alignfull)) {
  margin-left: auto;
  margin-right: auto;
  max-width: var(--wp--style--global--wide-size);
}